.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  color: white;
  background-color: transparent; /* No background */
}

.navbar-link {
  color: rgb(134, 133, 133);
  text-decoration: none;
  font-size: 16px;
  margin: 5px 10px; /* Adjust margin as needed */
  line-height: 1; /* Ensure the links are on the same baseline */
}

.navbar-contact {
  margin-right:0 auto; /* Push تواصل to the left */
  margin-bottom: 20px;
  cursor: pointer;
}

.navbar-login {
  margin-right: auto; /* Push عودة الى التسجيل to the right */
  margin-bottom: 20px;
  cursor: pointer;
}

.navbar-link:hover {
  text-decoration: underline;
}

.navbar-search {
  
  display: flex;
  justify-content: center;
  align-items: center; /* Align items vertically */
   /* Adjust the margin to move the search bar to the right */
}
