/* PostsView.css */

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  right: 0; /* Position the dropdown menu on the right */
  background-color: rgba(249, 249, 249, 0.9); /* Change the background color to a more transparent one */
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  border-radius: 15px; /* Add this line to make the border circular */
}

.dropdown-content button {
  color: black; /* Change the text color */
  padding: 12px 16px; /* Change the padding */
  text-decoration: none;
  display: block;
  background-color: #f9f9f9; /* Add this line to change the background color */
  border: none; /* Add this line to remove the border */
}

.dropdown-content button:hover {
  background-color: #f1f1f1; /* Change the background color on hover */
}

.dropdown:hover .dropdown-content {display: block;}

.three-dots {
